<template>
  <div class="view-container">
    <div class="row breadcrumb-container">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{path:'/bm'}"></el-breadcrumb-item>
        <el-breadcrumb-item :to="{path:'/edu-record-list'}"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="row main-page">
      <div id="print">
        <div class="print">
          <div class="title"></div>
          <div class="con1 row">
            <div class="info">
              <div class="info-item row">
                <span> {{data.realname}}</span>
                <span> {{data.licenseType}}</span>
              </div>
              <div class="info-item row">
                <span>{{data.licenseNo}}</span>
                <span>:{{data.idNo}}</span>
              </div>
            </div>
            <div class="img">
              <img :src="data.headUrl" id="ossImg">
            </div>
          </div>
          <div class="con2 row">
            {{moment2(data.createTime)}}
          </div>
          <div class="con3">
            <div class="row"></div>
            <div class="row"></div>
            <div class="row"> &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</div>
          </div>
          <div class="con4">
            <div class="title"></div>
            <div class="row">
              
            </div>
          </div>
          <div class="con5">
            
          </div>
        </div>
      </div>
      <el-button type="primary" @click="print()"></el-button>
      <el-button type="primary" @click="saveDownload()"></el-button>
    </div>

    <div id="ssssssssssss" style="position: fixed;right:0px;top:0px;width:600px;background-color:#333333;"></div>

  </div>

</template>
<script>
import moment from 'moment'
import html2canvas from 'html2canvas'
import baseService from '../../service/baseService.js'
export default {
  data() {
    return {
      imgObj: null,
      data: {},
      path: {
        detail: '/edurecord/detail'
      }
    }
  },
  computed: {
    dataAuditStatus() {
      return this.$store.state.data.dataAuditStatus
    },
    dataIdType() {
      return this.$store.state.data.dataIdType
    }
  },
  mounted() {
    this.getData()
  },
  methods: {
    // 
    moment(time) {
      return moment(time).format('YYYY-MM-DD HH:mm:ss')
    },
    moment2(time) {
      return moment(time).format('YYYY-MM-DD')
    },
    getData() {
      let id = this.$route.query.id
      baseService.baseGetData(this.path.detail, id).then(res => {
        let result = res.data
        if (result.status !== '0x0000') {
          this.$message({
            message: result.message,
            type: 'warning'
          })
        }
        this.data = res.data.data
      })
    },
    print() {
      let subOutputRankPrint = document.getElementById('print')
      let newContent = subOutputRankPrint.innerHTML
      let oldContent = document.body.innerHTML
      document.body.innerHTML = newContent
      window.print()
      window.location.reload()
      return false
    },
    saveDownload() {
      html2canvas(document.getElementById('print')).then(canvas => {
        var img = new Image()
        img.src = document.getElementById('ossImg').src
        document.getElementById('ssssssssssss').appendChild(canvas)
        let dataURL = canvas.toDataURL('image/png')
        var filename = 'fy' + new Date().getTime() + '.png'
        this.saveFile(dataURL, filename)
      })
    },
    saveFile(data, filename) {
      var save_link = document.createElementNS('http://www.w3.org/1999/xhtml', 'a')
      save_link.href = data
      save_link.download = filename

      var event = document.createEvent('MouseEvents')
      event.initMouseEvent('click', true, false, window, 0, 0, 0, 0, 0, false, false, false, false, 0, null)
      save_link.dispatchEvent(event)
    }
  }
}
</script>

<style>
#ssssssssssss canvas {
  display: none;
}
.print {
  margin-top: 20px;
}
.print .title {
  text-align: center;
  font-size: 16px;
  font-weight: bold;
  height: 20px;
  line-height: 20px;
}
.con1,
.con2 {
  display: flex;
  padding: 40px;
  box-sizing: border-box;
}
.info {
  flex: 1;
}
.img {
  width: 90px;
  height: 120px;
  box-sizing: border-box;
}
.img img {
  width: 100%;
  height: 100%;
}
.info-item {
  display: flex;
}
.info-item span {
  flex: 1;
}
.con3,
.con4,
.con5 {
  padding: 40px;
  box-sizing: border-box;
}
.con3 div {
  text-align: right;
}
.con5 {
  text-align: right;
  margin-right: 100px;
}
</style>




